  overflow: hidden;
}
#app {
  width: calc(35% + 520px);
  height: 100%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
  grid-gap: 10px;
}
body {
  height: 100vh;
}
body, html {
  padding: 0px;
  margin: 0px;
  width: 100%;
  background-color: #e6f7f7;
  font: 1em sans-serif;
}
