#albumcontainer {
  overflow: hidden;
  width: 100%;
  height: 100%;
}
#album {
  height: 100%;
  width: 100%;
  overflow: scroll;
  overflow-x: hidden;
  width: calc(100% + 1vw);
  /* background-color: white; */
}
.item {
  height: auto;
  text-align: left;
  padding: 0;
  width: 100%;
  float: right;
  height: 200px;
  position: relative;
  padding: 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  /* background-color: red; */
  border-top: 1px solid #C63B41;
  border-bottom: 1px solid #C63B41;
  /* background-color: rgb(230, 226, 193); */
}
.item:nth-child(even) {
  /* background-color: red; */
  float: left;
  text-align: right;
}
.image {
  height: 100%;
  float: right;
  right: 0%;
}
.item:nth-child(even) .image {
  float: left;
  left: 0%;
}
.item img {
  height: 100%;
  width: auto;
}
.item:nth-child(even) .purchase {
  float: left;
  left: 0%;
}
body {
  overflow: hidden;
}
.anim {
  -webkit-transition: all .5s;
  transition: all .5s;
}
.address {
  width: 100%;
}
@media (max-width: 640px) {
.center {
    grid-column-start: 1;
